<template>
  <section class="p-6">
    <!-- üî• Ï†ÑÏ≤¥ ÎÇ¥Ïö©ÏùÑ Í∞ÄÏö¥Îç∞Î°ú Î™®ÏúºÎäî ÎûòÌçº -->
    <div class="max-w-5xl mx-auto">
      <div class="form-actions">
        <h2
          class="text-2xl md:text-3xl font-bold tracking-tight whitespace-nowrap align-middle"
        >
          Î≤ÑÏ†ÑÎ≥Ñ Î™©Î°ù
        </h2>

        <MaterialButton color="dark" size="sm" @click="goToNew">
          Ï°∞ÏÇ¨ÏßÄ Ï†úÏûëÌïòÍ∏∞
        </MaterialButton>
      </div>

      <div v-if="loading">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
      <div v-else-if="error" class="text-red-600">{{ error }}</div>

      <!-- üî• Ïπ¥Îìú ÌòïÌÉú ÌÖåÏù¥Î∏î -->
      <div v-else class="table-card">
        <table class="nice-table">
          <thead>
            <tr>
              <th class="th-cell">ÌÖúÌîåÎ¶øÏΩîÎìú</th>
              <th class="th-cell">ÌÖúÌîåÎ¶øÎ≤ÑÏ†ÑÏΩîÎìú</th>
              <th class="th-cell">Î≤ÑÏ†ÑÎ≤àÌò∏</th>
              <th class="th-cell">Î≤ÑÏ†ÑÏÑ∏Î∂ÄÎ≤àÌò∏</th>
              <th class="th-cell">ÏÇ¨Ïö©Ïó¨Î∂Ä</th>
              <th class="th-cell">Ïú†Ìö®ÏãúÏûëÏùº</th>
              <th class="th-cell">Ïú†Ìö®Ï¢ÖÎ£åÏùº</th>
            </tr>
          </thead>

          <tbody>
            <tr
              v-for="row in paginatedData"
              :key="row.template_ver_code"
              class="table-row-item"
              :class="{ 'is-current': row.is_current === 'Y' }"
              @click="goToDetail(row.template_ver_code)"
            >
              <td class="td-cell mono">{{ row.template_code }}</td>
              <td class="td-cell mono">{{ row.template_ver_code }}</td>
              <td class="td-cell">{{ row.version_no }}</td>
              <td class="td-cell">{{ row.version_detail_no }}</td>

              <td class="td-cell">
                <span v-if="row.is_current === 'Y'" class="badge badge-active">
                  ÏÇ¨Ïö©Ï§ë
                </span>
                <span v-else class="badge badge-inactive"> ÎØ∏ÏÇ¨Ïö© </span>
              </td>

              <td class="td-cell">
                {{ row.effective_from?.slice?.(0, 10) || "-" }}
              </td>
              <td class="td-cell">
                {{ row.effective_to?.slice?.(0, 10) || "-" }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò -->
      <div class="mt-6 text-center">
        <MaterialPagination color="dark" size="md">
          <MaterialPaginationItem
            prev
            :disabled="currentPage === 1"
            @click="changePage(currentPage - 1)"
          />
          <MaterialPaginationItem
            v-for="page in totalPages"
            :key="page"
            :label="page"
            :active="page === currentPage"
            @click="changePage(page)"
          />
          <MaterialPaginationItem
            next
            :disabled="currentPage === totalPages"
            @click="changePage(currentPage + 1)"
          />
        </MaterialPagination>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import MaterialPagination from "@/components/MaterialPagination.vue";
import MaterialPaginationItem from "@/components/MaterialPaginationItem.vue";
import MaterialButton from "@/components/MaterialButton.vue";

const router = useRouter();
const list = ref([]);
const loading = ref(false);
const error = ref(null);

const currentPage = ref(1);
const pageSize = 10;

const totalPages = computed(() => Math.ceil(list.value.length / pageSize));

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * pageSize;
  return list.value.slice(start, start + pageSize);
});

function changePage(page) {
  if (page < 1 || page > totalPages.value) return;
  currentPage.value = page;
}

onMounted(async () => {
  loading.value = true;
  try {
    const { data } = await axios.get("/api/survey");
    list.value = Array.isArray(data.result) ? data.result : [];
  } catch (e) {
    error.value = e.message || "Î™©Î°ù Ï°∞Ìöå Ï§ë Ïò§Î•ò";
  } finally {
    loading.value = false;
  }
});

function goToNew() {
  router.push("/survey/new");
}

function goToDetail(templateVerCode) {
  router.push({
    name: "survey-detail-by-ver",
    params: { templateVerCode },
  });
}
</script>

<style scoped>
section {
  color: #111827;
  font-size: 15px; /* Ï†ÑÏ≤¥ Í∏∞Î≥∏ Ìè∞Ìä∏ ÌÅ¨Í∏∞ */
}

.pagination {
  display: inline-flex !important;
}

.form-actions {
  padding-bottom: 0.9rem;
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
}

/* Ïπ¥Îìú Ïª®ÌÖåÏù¥ÎÑà */
.table-card {
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
  box-shadow: 0 8px 18px rgba(15, 23, 42, 0.06);
  overflow: hidden;
}

/* ÌÖåÏù¥Î∏î */
.nice-table {
  width: 100%;
  table-layout: fixed; /* ÏπºÎüº Ìè≠ Í∑†Îì± Î∂ÑÎ∞∞ */
  border-collapse: collapse;
  text-align: center; /* ‚òÖ Ï†ÑÏ≤¥ Ï§ëÏïô Ï†ïÎ†¨ */
}

/* Ìó§Îçî ÏÖÄ */
.th-cell {
  padding: 0.75rem 0.9rem;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #6b7280;
  background: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap;
  text-align: center; /* ‚òÖ Ï§ëÏïô Ï†ïÎ†¨ */
}

@media (max-width: 900px) {
  .th-cell {
    white-space: normal; /* Ï§ÑÎ∞îÍøà ÌóàÏö© */
    font-size: 13px;
    line-height: 1.3;
    padding: 0.4rem 0.5rem;
  }
}

/* Î∞îÎîî ÏÖÄ */
.td-cell {
  padding: 0.7rem 0.9rem;
  border-bottom: 1px solid #f3f4f6;
  color: #111827;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 15px;
  text-align: center; /* ‚òÖ Ï§ëÏïô Ï†ïÎ†¨ */
}

.td-cell.mono {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
  font-size: 15px;
  color: #4b5563;
  text-align: center; /* ‚òÖ Ï§ëÏïô Ï†ïÎ†¨ */
}

/* Ìñâ */
.table-row-item {
  transition:
    background-color 0.15s ease,
    box-shadow 0.15s ease,
    transform 0.1s ease;
  cursor: pointer;
}

/* Ï§ÑÎ¨¥Îä¨ */
.table-row-item:nth-child(odd) {
  background-color: #ffffff;
}
.table-row-item:nth-child(even) {
  background-color: #f9fafb;
}

/* Ìò∏Î≤Ñ */
.table-row-item:hover {
  background-color: #f3f4f6;
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(15, 23, 42, 0.08);
}

/* ÏÇ¨Ïö©Ï§ë Í∞ïÏ°∞ */
.table-row-item.is-current {
  border-left: 3px solid #111827;
  background: linear-gradient(
    to right,
    rgba(17, 24, 39, 0.06),
    rgba(249, 250, 251, 1)
  );
}

/* Î™®Îì† ÌÖåÏù¥Î∏î ÏÖÄ Í≥µÌÜµ */
table th,
table td {
  font-size: 15px !important;
  font-weight: 400;
  font-family: "Noto Sans KR", sans-serif;
  text-align: center !important; /* ‚òÖ Î¨¥Ï°∞Í±¥ Ï§ëÏïô */
}

/* Î±ÉÏßÄ */
.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.32rem 0.75rem;
  border-radius: 999px;
  font-size: 13px; /* ÏÇ¥Ïßù ÏûëÍ≤å */
  font-weight: 500;
  border: 1px solid transparent;
  min-width: auto; /* Í∏∏Ïù¥ Í∞ïÏ†úÌïòÏßÄ ÏïäÏùå */
  line-height: 1.1; /* Îçî ÏΩ§Ìå©Ìä∏ÌïòÍ≤å */
  height: auto;
}

.badge-active {
  background-color: #deeec8 !important; /* green pastel */
  border: 1px solid #bedca0 !important;
  color: #3f7a3a !important;
}

.badge-inactive {
  background-color: #e4e6e1 !important; /* gray pastel */
  border: 1px solid #d0d3cd !important;
  color: #4b5563 !important;
}
</style>
